*, *:before, *:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Ubuntu', 'Helvetica Neue', sans-serif;
  font-size: 14px;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

h2, h3 {
  font-weight: 400;
}


h2 button, h3 button {
  font-size: 13px;
}

textarea,
input[type="text"] {
  font-size: inherit;
  font-family: inherit;
  border-radius: 3px;
  border: 1px solid #CCC;
  padding: 8px 10px;
  width: 100%;
}

button {
  font-size: inherit;
  font-family: inherit;
  border: 0;
  border-radius: 3px;
  padding: 8px 15px;
}

